import React from 'react';
import { Animated, PanResponder, Dimensions, StyleSheet, Text } from 'react-native';
import { Card } from './Card';

const SCREEN_WIDTH = Dimensions.get('window').width;
const SWIPE_THRESHOLD = SCREEN_WIDTH * 0.25;

export const SwipeCard = ({ data, onSwipeLeft, onSwipeRight }) => {
  const position = new Animated.ValueXY();
  const rotate = position.x.interpolate({
    inputRange: [-SCREEN_WIDTH * 1.5, 0, SCREEN_WIDTH * 1.5],
    outputRange: ['-30deg', '0deg', '30deg']
  });

  const likeOpacity = position.x.interpolate({
    inputRange: [0, SCREEN_WIDTH * 0.25],
    outputRange: [0, 1]
  });

  const dislikeOpacity = position.x.interpolate({
    inputRange: [-SCREEN_WIDTH * 0.25, 0],
    outputRange: [1, 0]
  });

  const panResponder = PanResponder.create({
    onStartShouldSetPanResponder: () => true,
    onPanResponderMove: (_, gesture) => {
      position.setValue({ x: gesture.dx, y: gesture.dy });
    },
    onPanResponderRelease: (_, gesture) => {
      if (gesture.dx > SWIPE_THRESHOLD) {
        forceSwipe('right');
      } else if (gesture.dx < -SWIPE_THRESHOLD) {
        forceSwipe('left');
      } else {
        resetPosition();
      }
    }
  });

  const forceSwipe = (direction) => {
    const x = direction === 'right' ? SCREEN_WIDTH : -SCREEN_WIDTH;
    Animated.timing(position, {
      toValue: { x, y: 0 },
      duration: 250,
      useNativeDriver: false
    }).start(() => {
      position.setValue({ x: 0, y: 0 });
      direction === 'right' ? onSwipeRight() : onSwipeLeft();
    });
  };

  const resetPosition = () => {
    Animated.spring(position, {
      toValue: { x: 0, y: 0 },
      useNativeDriver: false
    }).start();
  };

  const cardStyle = {
    transform: [
      { translateX: position.x },
      { translateY: position.y },
      { rotate }
    ]
  };

  return (
    <Animated.View style={[styles.container, cardStyle]} {...panResponder.panHandlers}>
      <Animated.View style={[styles.overlay, styles.likeOverlay, { opacity: likeOpacity }]}>
        <Text style={[styles.overlayText, { color: '#4CAF50' }]}>AGREE</Text>
      </Animated.View>
      <Animated.View style={[styles.overlay, styles.dislikeOverlay, { opacity: dislikeOpacity }]}>
        <Text style={[styles.overlayText, { color: '#F44336' }]}>DISAGREE</Text>
      </Animated.View>
      <Card data={data} />
    </Animated.View>
  );
};

const styles = StyleSheet.create({
  container: {
    width: '100%',
    height: '60%',
    alignItems: 'center',
    justifyContent: 'center',
    position: 'relative'
  },
  overlay: {
    position: 'absolute',
    padding: 12,
    borderWidth: 3,
    borderRadius: 8,
    zIndex: 2,
    backgroundColor: 'rgba(255,255,255,0.9)',
    top: '40%',
    transform: [{ translateY: -25 }]
  },
  likeOverlay: {
    right: '50%',
    transform: [
      { translateX: 100 },
      { translateY: -25 }
    ],
    borderColor: '#4CAF50',
  },
  dislikeOverlay: {
    left: '50%',
    transform: [
      { translateX: -100 },
      { translateY: -25 }
    ],
    borderColor: '#F44336',
  },
  overlayText: {
    fontSize: 28,
    fontWeight: 'bold',
    letterSpacing: 1,
  }
});
